* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  height: 2000px;
}

.header {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #ffffff;
  background: #24282f;
  &__logo {
    margin: 38px 20px;
    padding-left: 55px;
    text-transform: uppercase;
    font: 600 26px/24px 'Raleway', 'Arial';
    background: url('./assets/img/icon_logo.svg') no-repeat left center;
  }
}

.nav {
  display: flex;
  &__item {
    display: flex;
    align-items: center;
    margin: 0 15px;
    &.active {
      border-bottom: 2px solid #cc9966;
    }
    &__router-link {
      padding: 0 15px;
      text-decoration: none;
      text-transform: uppercase;
      font: 400 14px/24px 'Open Sans', 'Arial';
      color: #ffffff;
    }
  }
}

.main {
  width: 100%;
}

.about {
  &__head-layout {
    margin-bottom: 155px;
    width: 100%;
    height: 385px;
    background: url('./assets/img/header_layout.jpg') no-repeat;
    &__shadow {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      background: rgba(36, 40, 47, 0.96);
      &__title {
        margin: 0 20px;
        font: 700 40px/55px 'Raleway', 'Arial';
        color: #ffffff;
      }
    }
  }
}

.cards {
  display: flex;
  flex-direction: column;
  &__title {
    position: relative;
    margin-bottom: 35px;
    padding-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
    font: 600 24px 'Raleway', 'Arial';
    color: #24282f;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%);
      display: block;
      width: 78px;
      height: 2px;
      background-color: #b5a263;
    }
  }

  &__container {
    display: flex;
    justify-content: center;
    .item {
      margin: 0 50px 40px;
      &__img {
        width: 320px;
        margin-bottom: 28px;
        height: 320px;
        &.first {
          background: url('./assets/img/card_photo_1.jpg') no-repeat;
          background-size: cover;
        }
        &.second {
          background: url('./assets/img/card_photo_2.jpg') no-repeat;
          background-size: cover;
        }
        &.third {
          background: url('./assets/img/card_photo_3.jpg') no-repeat;
          background-size: cover; 
        }
      }
      &__info {
        display: flex;
        justify-content: space-between;
        margin: 0 10px;
        &__user {
          display: flex;
          flex-direction: column;
          &__name {
            font: 500 16px 'Raleway', 'Arial';
            text-transform: uppercase;
            color: #232323;
          }
          &__post {
            font: 400 14px 'Open Sans', 'Arial';
            color: #b5a263;
          }
        }
        &__social-media {
          display: inline-flex;
          .icon {
            display: block;
            margin: 0 4px;
            width: 34px;
            height: 34px;
          }
          &__fb {
            background: url('./assets/img/icon_facebook.svg') no-repeat center;
          }
          &__tw {
            background: url('./assets/img/icon_twitter.svg') no-repeat center;
          }
          &__gp {
            background: url('./assets/img/icon_google.svg') no-repeat center;
          }
        }
      }
    }
  }

  &__show-more-btn {
    margin: 0 auto 30px;
    padding: 0 40px;
    border: 1px solid #24282f;
    border-radius: 25px;
    font: 400 16px/50px 'Open Sans', 'Arial';
    text-transform: uppercase;
    color: #24282f;
    cursor: pointer;
  }
}

.slider {
  padding-top: 115px;
  background: #f7f7f7;
  &__title {
    position: relative;
    margin-bottom: 30px;
    padding-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
    font: 600 24px 'Raleway', 'Arial';
    color: #24282f;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%);
      display: block;
      width: 78px;
      height: 2px;
      background: #24282f;
    }
  }
  &__reviews {
    margin: 0 auto;
    padding: 0 100px;
    width: 80%;
    .glide {
      &__slides {
        align-items: center;
      }
      &__slide {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 0;
        &__img {
          margin-bottom: 35px;
          border-radius: 50%;
          width: 90px;
          height: 90px;
          &.first {
            background: url('./assets/img/photo_review.jpg') no-repeat center;
          }
          &.second {
            background: url('./assets/img/photo_review_2.jpg') no-repeat center;
          }
          &.third {
            background: url('./assets/img/photo_review_3.jpg') no-repeat center;
          }
        }
        &__reviews-text {
          margin: 0 50px 35px;
          text-align: center;
          font: 300 14px/24px 'Open Sans', 'Arial';
          color: #000000;
        }
        &__name {
          margin-bottom: 10px;
          text-transform: uppercase;
          font: 600 16px/24px 'Open Sans', 'Arial';
          color: #b5a263;
        }
        &__post {
          margin-bottom: 10px;
          font: 400 14px/24px 'Open Sans', 'Arial';
          color: #000000;
        }
      }
      &__arrow {
        border: none;
        border-radius: 50%;
        padding: 0;
        box-shadow: none;
        width: 40px;
        height: 40px;
        &:hover {
          border: none;
          box-shadow: 0 4px 14px 1px rgba(0, 0, 0, 0.1)
        }
        &--prev {
          left: -95px;
          background: url('./assets/img/arrow_prev.png') no-repeat center;
        }
        &--next {
          right: -95px;
          background: url('./assets/img/arrow_next.png') no-repeat center;
        }
      }
    }
  }
}

.footer {
  width: 100%;
  height: 400px;
  background: #cc9966;
}